@import url("https://netdna.bootstrapcdn.com/font-awesome/4.1.0/css/font-awesome.min.css");
.duration {
  transition-duration: .3s;
}
.flex-container {
  display: flex;
  align-items: center;
  height: 100%;
}
.container {
  padding: 0 20px;
  margin: 0 auto;
  height: 100%;
  width: 1170px;
}
.mobile {
  display: none;
}
$aqua: #62bdbd;
$aqua-dark: #62a29e;
$aqua-darkest: #528b86;
$red: #e8645a;
$red-darkest: #e2534b;
$global-color: #777;
$light-grey: #b0b8b9;
$grey: #7f8c8c;
$black: #3f4646;

header {
  background: $aqua url("/img/header-bg.png") 0 100% repeat-x;
  padding-bottom: 10px;
  height: 80px;
  .container {
    justify-content: space-between;
    flex-wrap: wrap;
  }
  .header__right {
    @extend .flex-container;
    flex: 2;
    align-items: center;
    justify-content: space-between;
  }
  nav {
    flex: 1;
    >ul {
      @extend .flex-container;
      justify-content: space-around;
      position: relative;
      li {
        @extend .duration;
        box-shadow: 0 0 0 0 rgba(#fff, 0);
        list-style: none;
        border-radius: 5px;
        &.active {
          background-color: $aqua-dark;
          box-shadow: 0 3px 0 0 rgba($aqua-darkest, 1);
        }
        a {
          display: block;
          color: #fff;
          text-transform: uppercase;
          font-weight: 600;
          padding: 10px 15px;
        }
        &:hover {
          background-color: $red;
          box-shadow: 0 3px 0 0 rgba($red-darkest, 1);
        }
        &.has-child {
          > a {
            padding-left: 20px;
            position: relative;
            &:before {
              content: '';
              background: url('/img/arrow-down.svg') 0 0 no-repeat;
              height: 8px;
              width: 10px;
              position: absolute;
              top: 50%;
              transform: translateY(-50%) rotate(0deg);
              left: 5px;
              @extend .duration;
            }
          }
          @media screen and (min-width: 768px){
            &:hover {
              a {
                &:before {
                  transform: translateY(-50%) rotate(180deg);
                }
              }
            }
          }
          .submenu {
            @extend .duration;
            a {
              color: #fff;
              text-transform: none;
            }
          }
          @media screen and (min-width: 768px) {
            .submenu {
              background-color: $red-darkest;
              border-radius: 5px;
              padding: 5px;
              position: absolute;
              opacity: 0;
              visibility: hidden;
              top: 30px;
              &:before {
                content: '';
                position: absolute;
                top: -6px;
                left: 10px;
                width: 0;
                height: 0;
                border-left: 10px solid transparent;
                border-right: 10px solid transparent;
                border-bottom: 0 solid $red-darkest;
                transition: all .3s ease-out .4s;
              }
              &:after {
                content: '';
                position: absolute;
                top: -20px;
                left: 0;
                background: transparent;
                height: 20px;
                width: 100%;
              }
            }
          }
          &:hover {
            @media screen and (min-width: 768px) {
              .submenu {
                opacity: 1;
                visibility: visible;
                top: 50px;
                z-index: 1;
                &:before {
                  border-bottom: 10px solid $red-darkest;
                }
                li {
                  background-color: $red;
                  padding-left: 10px;
                  position: relative;
                  transition-duration: .3s;
                  &:not(:last-of-type) {
                    margin-bottom: 5px;
                  }
                  a {
                    &:before {
                      content: '';
                      background: url('/img/arrow-down.svg') 0 0 no-repeat;
                      height: 8px;
                      width: 8px;
                      position: absolute;
                      top: 50%;
                      transform: translateY(-50%) rotate(-90deg);
                      left: 12px;
                    }
                  }
                  &:hover {
                    background-color: $aqua;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
  .search {
    flex: initial;
    cursor: pointer;
    height: 15px;
    width: 15px;
    @extend .duration;
    transform: scale(1);
    margin-left: 20px;
    svg {
      fill: rgba(#fff, 1);
      height: 100%;
      width: 100%;
    }
    &:hover {
      transform: scale(1.1);
    }
  }
}
body {
  background: #f3f3f3;
  color: $global-color;
  font-family: 'Open Sans', sans-serif;
  font-size: 13px;
  line-height: 1.2;
}
.logo {
  color: rgba(#fff, 1);
  flex: 1;
  @extend .duration;
  span {
    &:first-child {
      font-size: 30px;
      font-weight: 800;
    }
    &:last-child {
      font-size: 25px;
    }
  }
  &:hover {
    color: rgba(#fff, .7);
  }
}
.global-title {
  color: $grey;
  font-size: 30px;
  font-weight: 600;
  margin: 0 0 5px;
}
.title-small {
  color: $grey;
  display: block;
  font-size: 16px;
  font-weight: 800;
}
/*=========main============*/
.main__slider-top {
  margin-bottom: 50px;
  .slick-list {
    height: 500px;
  }
  .slide {
    background-color: $grey;
  }
  .description {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    padding: 0 80px;
    .slider-top__title-wrap {
      margin-bottom: 10px;
    }
    .slider-top__title {
      background: $red;
      border-radius: 5px;;
      display: inline-block;
      color: #fff;
      font-size: 50px;
      font-weight: 800;
      text-transform: uppercase;
      padding: 5px 10px;
      line-height: .8;
    }
    .slider-top__content {
      background: $black;
      display: inline-block;
      color: #fff;
      font-size: 16px;
      padding: 5px 30px;
      margin: 0;
      border-radius: 5px;
      max-width: 470px;
      line-height: 26px;
    }
  }
  .slick-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    box-shadow: 0 3px 0 0 rgba($red-darkest, 1);
    border: none;
    @extend .duration;
    height: 60px;
    width: 50px;
    cursor: pointer;
    z-index: 10;
    color: #fff;
    &.slick-prev {
      left: 0;
      background: $red url("/img/arrows-left.png") center center no-repeat;
      border-radius: 0 5px 5px 0;
    }
    &.slick-next {
      right: 0;
      background: $red url("/img/arrows-right.png") center center no-repeat;
      border-radius: 5px 0 0 5px;
    }
    &:hover {
      background-color: $aqua;
      box-shadow: 0 3px 0 0 rgba($aqua-darkest, 1);
    }
  }
  .slick-dots {
    text-align: center;
    margin-top: 20px;
    li {
      display: inline-block;
      margin: 0 5px;
      button {
        border: none;
        background-color: $light-grey;
        text-indent: -9999px;
        display: block;
        height: 5px;
        width: 60px;
        @extend .duration;
      }
      &.slick-active, &:hover {
        button {
          background-color: $red;
        }
      }
    }
  }
}
.main__services{
  height: 100px;
  justify-content: space-between;
  margin-bottom: 50px;
  .flex-container {
    padding: 0 20px;
    background-color: #ececec;
    border-radius: 5px;
  }
}
.main__services__title-wrap {
  flex: 1;
  padding-right: 40px;
  p {
    line-height: 1.5;
  }
}
.btn-wrap {
  text-align: center;
  .btn {
    background-color: $red;
    border-radius: 5px;
    box-shadow: 0 3px 0 0 $red-darkest;
    display: inline-block;
    text-transform: uppercase;
    padding: 15px 40px;
    font-size: 20px;
    color: #fff;
    @extend .duration;
    &:hover {
      background-color: $aqua;
      box-shadow: 0 3px 0 0 $aqua-darkest;
    }
  }
}
.main__about-services {
  justify-content: space-between;
  align-items: stretch;
  padding-bottom: 75px;
  .main__about-services__item {
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0 3px 0 0 #e5e6e6;
    text-align: center;
    padding: 50px 15px;
    display: flex;
    position: relative;
    width: 23%;
    a {
      color: #777;
      p {
        line-height: 1.5;
      }
    }
    .fa {
      color: $aqua;
      font-size: 60px;
      @extend .duration;
      margin-bottom: 10px;
    }
    .title-small {
      margin-bottom: 30px;
    }
    .btn-wrap{
      position: absolute;
      bottom: -20px;
      left: 50%;
      transform: translateX(-50%);
      width: 100%;
      .btn {
        font-size: 14px;
        text-transform: lowercase;
        padding-left: 55px;
        padding-right: 55px;
        background-color: $aqua;
        box-shadow: 0 3px 0 0 $aqua-darkest;
      }
    }
    &:hover {
      .fa {
        color: $red-darkest;
      }
      .btn {
        background-color: $red;
        box-shadow: 0 3px 0 0 $red-darkest;
      }
    }
  }
}
.why {
  background: -moz-linear-gradient(top, rgba(236,236,236,1) 0%, rgba(236,236,236,1) 45%, rgba(236,236,236,0) 46%, rgba(236,236,236,0) 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgba(236,236,236,1) 0%,rgba(236,236,236,1) 45%,rgba(236,236,236,0) 46%,rgba(236,236,236,0) 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, rgba(236,236,236,1) 0%,rgba(236,236,236,1) 45%,rgba(236,236,236,0) 46%,rgba(236,236,236,0) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ececec', endColorstr='#00ececec',GradientType=0 ); /* IE6-9 */
  padding: 70px 0;
  position: relative;
  text-align: center;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    background: url("/img/linear-top-bg.png") 0 0 repeat-x;
    width: 100%;
    height: 19px;
  }
  .title {
    color: $red-darkest;
    text-transform: uppercase;
    font-size: 40px;
    font-weight: 600;
    line-height: 1.5;
    margin: 0;
  }
  .why__text {
    margin-bottom: 65px;
    p {
      line-height: 1.7;
    }
  }
  .why__slider {
    margin-bottom: 50px;
    .slick-track {
      padding: 60px 0;
    }
    .slide {
      //background: url("/img/tool.png") no-repeat 0 0 / 100%;
      transform: scale(1);
      transition-duration: .2s;
      z-index: 1;
      position: relative;
      img {
        width: 100%;
      }
      .why_slider_container {
        background-color: $aqua;
        border-radius: 5px;
        transition-duration: .2s;
        position: absolute;
        top: 12%;
        left: 5px;
        right: 5px;
        bottom: 5px;
        span {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          color: #fff;
          font-size: 30px;
        }
      }
    }
    .slick-current+div {
      transform: scale(1.4);
      z-index: 2;
      .why_slider_container {
        background-color: $grey;
        transition-duration: .2s;
      }
    }
  }
}
.why__info {
  text-align: left;
  align-items: flex-start;
  .title-small {
    margin-bottom: 30px;
    line-height: 1.7;
  }
  ul {
    list-style: none;
    li {
      display: flex;
      margin-bottom: 10px;
      i {
        color: $red-darkest;
        margin-right: 5px;
      }
    }
  }
  .why__info__item-1, .why__info__item-3 {
    width: 45%;
  }
  .why__info__item-2 {
    padding: 0 3%;
    p {
      line-height: 1.7;
      span {
        color: $red-darkest;
        text-decoration: underline;
      }
      &:last-of-type {
        margin-bottom: 50px;
      }
    }
    .why__info__diagram {
      justify-content: space-between;
      flex-wrap: wrap;
      .diagram__item {
        text-align: center;
        .img {
          position: relative;
          margin-bottom: 20px;
          span {
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            bottom: 0;
            font-size: 30px;
            font-weight: 800;
          }
          img {
            display: block;
            width: 100%;
          }
        }
        .title-small {
          text-transform: uppercase;
        }
      }
    }
  }
  .why__info__item-3 {
    .text {
      padding-left: 15px;
      margin-bottom: 20px;
      line-height: 1.9;
    }
    .name {
      font-size: 16px;
      font-weight: 600;
      text-align: center;
    }
  }
}
.clients {
  position: relative;
  margin-bottom: 50px;
  .clients__header {
    margin-bottom: 30px;
    width: calc(100% - 125px);
    .title-small {
      flex: 0 0 150px;
    }
    .clients__decor-line {
      height: 1px;
      background: #e1e2e2;
      margin-left: 20px;
      position: relative;
      flex: 1;
      &:before {
        content: '';
        position: absolute;
        left: 0;
        height: 8px;
        width: 8px;
        border-radius: 50%;
        background-color: #e1e2e2;
        top: 50%;
        transform: translateY(-50%);
      }
    }
  }
  .clients__slider {
    .slide {
      text-align: center;
      img {
        display: inline-block;
      }
    }
    &.slick-slider {
      position: static;
    }
    .slick-arrow {
      background-color: $light-grey;
      border-radius: 5px;
      box-shadow: 0 3px 0 0 $grey;
      height: 40px;
      width: 40px;
      position: absolute;
      font-size: 42px;
      color: #fff;
      text-align: center;
      cursor: pointer;
      @extend  .duration;
      top: -14px;
      &:hover {
        background-color: $red;
        box-shadow: 0 3px 0 0 $red-darkest;
      }
      &.slick-next {
        right: 0;
      }
      &.slick-prev {
        right: 60px;
      }
    }
  }
}
footer {
  color: #fff;
  .footer__top {
    background: $aqua url("/img/linear-top-bg__opacity.png") 0 0 repeat-x;
    padding: 45px 0;
    .flex-container {
      justify-content: space-between;
      align-items: flex-start;
      .footer__top__item-1, .footer__top__item-4 {
        flex: 1 1 35%;
      }
      .footer__top__item-2, .footer__top__item-3 {
        flex: 1 1 15%;
        ul {
          list-style: none;
          margin-left: 25px;
          li {
            margin-bottom: 10px;
            a {
              color: #fff;
              @extend .duration;
              .fa {
                margin-right: 5px;
                @extend .duration;
                transform: rotate(0deg);
              }
              &:hover {
                .fa {
                  transform: rotate(180deg);
                }
              }
            }
          }
        }
        .footer__title {
          color: rgba(#000, .5);
          font-size: 20px;
          line-height: 1.9;
          margin-bottom: 30px;
          font-weight: 800;
          display: block;
        }
      }
      .footer__top__item-1 {
        padding-right: 40px;
        .logo {
          display: block;
          margin-bottom: 30px;
        }
        p {
          line-height: 1.7;
          &:last-of-type {
            margin-bottom: 30px;
          }
        }
        .phone, .email {
          display: flex;
          align-items: baseline;
          margin-bottom: 10px;
          span {
            display: inline-block;
            margin-right: 15px;
          }
          a {
            color: #fff;
            font-size: 20px;
            font-style: italic;
            font-weight: 600;
            @extend .duration
          }
          &:hover {
            a {
              color: rgba(#fff, .7);
            }
          }
        }
      }
      .footer__top__item-4 {
        padding-left: 40px;
        .footer__white-title {
          font-size: 20px;
          display: block;
          margin-bottom: 30px;
          font-weight: 900;
          line-height: 1.8;
          i {
            font-weight: 400;
          }
        }
        .item {
          color: #fff;
          &:not(:last-of-type) {
            margin-bottom: 20px;
          }
          .img {
            background-color: rgba(#000, 0.2);
            border-radius: 5px;
            height: 70px;
            margin-right: 15px;
            flex: 0 0 70px;
            font-size: 30px;
            display: flex;
            align-items: center;
            justify-content: space-around;
            color: rgba(#fff, .5);
          }
          .text {
            p {
              line-height: 1.7;
              &.date {
                color: #000;
              }
            }
          }
        }
      }
    }
  }
  .footer__bottom {
    background-color: #6ab3ac;
    height: 50px;
    .flex-container {
      justify-content: space-between;
      align-items: center;
    }
    .socials {
      list-style: none;
      li {
        a {
          height: 25px;
          display: block;
          margin: 0 10px;
          svg {
            fill: #fff;
            @extend .duration;
            height: 100%;
          }
          &:hover {
            svg {
              fill: rgba(#fff, .7);
            }
          }
        }
      }
    }
  }
}

//responsive

@media screen and (max-width: 1169px){
  .container {
    width: 100%;
  }
  .clients {
    .clients__slider {
      .slick-arrow {
        &.slick-prev {
          right: 80px;
        }
        &.slick-next {
          right: 20px;
        }
      }
    }
  }
}
@media screen and (max-width: 991px) {
  header nav > {
    ul {
      li {
        a {
          font-size: 12px;
          padding: 10px;
        }
        .has-child{
           &:hover .submenu li a:before {
             left: 8px;
           }
         }
      }
    }
  }
  .why__info {
    .why__info__item-1, .why__info__item-3 {
      flex: 1;
    }
    .why__info__item-2 {
      flex: 2;
      .why__info__diagram {
        justify-content: space-around;
        .diagram__item {
          width: 35%;
        }
      }
    }
  }
}
@media screen and (max-width: 767px) {
  .mobile {
    display: flex;
  }
  .hamb {
    width: 50px;
    height: 40px;
    position: relative;
    margin: 50px auto;
    transform: rotate(0deg);
    transition: .5s ease-in-out;
    cursor: pointer;
    span {
      display: block;
      position: absolute;
      height: 4px;
      width: 100%;
      background: #fff;
      border-radius: 9px;
      opacity: 1;
      left: 0;
      transform: rotate(0deg);
      transition: .25s ease-in-out;
      &:nth-child(1) {
        top: 5px;
      }
      &:nth-child(2), &:nth-child(3) {
        top: 18px;
      }
      &:nth-child(4) {
        top: 31px;
      }
    }
    &.open {
      span {
        &:nth-child(1) {
          top: 18px;
          width: 0;
          left: 50%;
        }
        &:nth-child(2) {
          transform: rotate(45deg);
        }
        &:nth-child(3) {
          transform: rotate(-45deg);
        }
        &:nth-child(4) {
          top: 18px;
          width: 0;
          left: 50%;
        }
      }
    }
  }
  header {
    position: relative;
    .logo {
      flex: 12;
    }
    .header__menu {
      position: absolute;
      z-index: 0;
      top: 95%;
      background: $aqua;
      left: 0;
      right: 0;
      opacity: 0;
      visibility: hidden;
      transition-duration: .3s;
      ul {
        flex-direction: column;
        li {
          border-radius: 0;
          text-align: center;
          width: 100%;
          transition-duration: 0s;
          &.has-child {
            .submenu {
              height: 0;
              visibility: hidden;
              transition-duration: 0s;
              &.open{
                height: auto;
                visibility: visible;
              }
            }
            a {
              display: inline-block;
              &:before {
                left: 0;
              }
            }
          }
        }
      }
      &.open {
        z-index: 20;
        top: 100%;
        opacity: 1;
        visibility: visible;
      }
    }
    .search {
      height: 30px;
      width: 50px;
    }
  }
  .main__slider-top {
    .slick-arrow {
      background-size: 65%!important;
      width: 30px;
    }
    .description {
      padding: 0 40px;
    }
  }
  .main__slider-top .slick-list {
    height: 400px;
  }
  .main__services {
    height: auto;
    margin-bottom: 20px;
    .flex-container {
      flex-direction: column;
      justify-content: space-around;
      padding: 20px;
    }
    .main__services__title-wrap {
      flex: 1;
      padding: 0;
      margin-bottom: 20px;
      text-align: center;
    }
  }
  .main__about-services {
    flex-wrap: wrap;
    padding-bottom: 0;
    .main__about-services__item {
      margin-bottom: 40px;
      width: 47%;
    }
  }
  .why {
    background: none;
    padding: 20px 0;
    margin-bottom: 30px;
    &:after {
      display: none;
    }
    .why__slider {
      .slick-track {
        padding: 0;
      }
      .slick-current + div {
        transform: scale(1);
      }
    }
    .why__text {
      margin-bottom: 20px;
    }
  }
  .why__info {
    flex-direction: column;
    text-align: center;
    .why__info__item-1,.why__info__item-2,.why__info__item-3 {
      width: 100%;
    }
    .why__info__item-1 {
      ul {
        li {
          justify-content: center;
        }
      }
    }
    .why__info__item-2 {
      .why__info__diagram {
        .diagram__item {
          width: 23%;
        }
      }
    }
  }
  .clients {
    margin-bottom: 20px;
  }
  footer {
    .footer__top {
      .flex-container {
        >* {
          display: none;
        }
        .footer__top__item-1 {
          display: block;
          text-align: center;
          padding: 0;
          .logo  {
            margin-bottom: 20px;
          }
          .phone, .email {
            justify-content: center;
          }
          .phone a, .email span {
            font-size: 16px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 639px){
  .main__slider-top {
    .slick-list {
      height: 300px;
    }
    .description {
      .slider-top__title {
        font-size: 40px;
      }
    }
    .slick-arrow {
      background-size: 50% !important;
      width: 20px;
    }
  }
  .why__info .why__info__item-2 .why__info__diagram .diagram__item {
    width: 40%;
  }
}
@media screen and (max-width: 479px){
  .container {
    padding: 0 10px;
  }
  header {
    .logo {
      position: absolute;
      top: 17px;
      left: 50%;
      transform: translateX(-50%);
    }
    .search {
      height: 20px;
      width: 35px;
    }
  }
  .hamb {
    width: 35px;
    height: 25px;
    margin: 0;
    span {
      &:nth-child(1) {
        top: 0;
      }
      &:nth-child(2), &:nth-child(3) {
        top: 10px;
      }
      &:nth-child(4) {
        top: 20px;
      }
    }
    &.open {
      span {
        &:nth-child(1) {
          top: 18px;
          width: 0;
          left: 50%;
        }
        &:nth-child(2) {
          transform: rotate(45deg);
        }
        &:nth-child(3) {
          transform: rotate(-45deg);
        }
        &:nth-child(4) {
          top: 18px;
          width: 0;
          left: 50%;
        }
      }
    }
  }
  .main__slider-top .description {
    .slider-top__title {
      font-size: 30px;
    }
    .slider-top__content {
      font-size: 14px;
      max-width: 320px;
      line-height: 1.4;
    }
  }
  .main__about-services .main__about-services__item {
    width: 100%;
  }
  .why__info .why__info__item-2 .why__info__diagram .diagram__item {
    width: 100%;
  }
}